:root {
  --light-green-gradient: linear-gradient(90deg, rgba(65,255,105,1) 0%, rgba(66,236,232,1) 100%);
  --dark-green-gradient: linear-gradient(90deg, rgba(0,102,22,1) 0%, rgba(0,171,169,1) 100%);
  --complimentary-gradient: linear-gradient(90deg, #eea490 30%, #a490ee 70%);
  --light-green: rgba(144,238,164,1);
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
